import { useEffect, useRef } from "react";

export function getCookie(name: string) {
  const matches = document.cookie.match(
    new RegExp(
      "(?:^|; )" + name.replace(/([.$?*|{}()[]\\\/+^])/g, "\\$1") + "=([^;]*)"
    )
  );
  return matches ? decodeURIComponent(matches[1]) : undefined;
}

export function setCookie(
  name: string,
  value: string,
  props: {
    expires?: number | Date | string;
    path?: string;
  } = {}
) {
  let exp = props.expires;
  if (typeof exp == "number" && exp) {
    const d = new Date();
    d.setTime(d.getTime() + exp * 1000);
    exp = props.expires = d;
  }
  if (exp && exp instanceof Date) {
    props.expires = exp.toUTCString();
  }
  props.path = "/";
  value = encodeURIComponent(value);
  let updatedCookie = name + "=" + value;
  for (const propName in props) {
    updatedCookie += "; " + propName;
    const propValue = (props as { [key: string]: string | boolean })[propName];
    if (propValue !== true) {
      updatedCookie += "=" + propValue;
    }
  }
  document.cookie = updatedCookie;
}

export function deleteCookie(name: string) {
  setCookie(name, "", { expires: -1 });
}

export function isEqual(obj1: any, obj2: any) {
  //     obj1
  for (const p in obj1) {
    //  ,    
    if (obj1.hasOwnProperty(p) !== obj2.hasOwnProperty(p)) return false;

    switch (typeof obj1[p]) {
      //       :
      case "object":
        if (!isEqual(obj1[p], obj2[p])) return false;
        break;
      //    function:
      case "function":
        if (
          typeof obj2[p] == "undefined" ||
          (p !== "compare" && obj1[p].toString() !== obj2[p].toString())
        )
          return false;
        break;
      //  :
      default:
        if (obj1[p] !== obj2[p]) return false;
    }
  }

  //   obj2   :
  for (const p in obj2) {
    if (typeof obj1[p] == "undefined") return false;
  }
  return true;
}

/**
 *    
 * @param {*} n 
 * @param {Array} text_forms    [, , ]
 * @returns
 */
function declensionOfNum(n: number, text_forms: [string, string, string]) {
  n = Math.abs(n) % 100;
  var n1 = n % 10;
  if (n > 10 && n < 20) {
    return text_forms[2];
  }
  if (n1 > 1 && n1 < 5) {
    return text_forms[1];
  }
  if (n1 === 1) {
    return text_forms[0];
  }
  return text_forms[2];
}

export function formatPastDate(dateStr: string) {
  const today = new Date();
  today.setHours(0);
  today.setMinutes(0);
  today.setSeconds(0);

  const date = new Date(dateStr);
  const timezone = date.getHours() - date.getUTCHours();
  const hours = date.getHours().toString().padStart(2, "0");
  const minutes = date.getMinutes().toString().padStart(2, "0");

  let past = "";

  if (date.getTime() > today.getTime() + 24 * 3600 * 1000) {
    past = date.toLocaleDateString();
  } else if (date > today) {
    past = "";
  } else if (date.getTime() > today.getTime() - 24 * 3600 * 1000) {
    past = "";
  } else {
    const days = Math.ceil(
      (today.getTime() - date.getTime()) / (24 * 3600 * 1000)
    );
    past = `${days} ${declensionOfNum(days, ["", "", ""])} `;
  }

  return `${past}, ${hours}:${minutes} i-GMT${
    timezone > 0 ? "+" : ""
  }${timezone}`;
}

export const timingFunctions = {
  linear(k: number) {
    return k;
  },
  easeIn(k: number) {
    return Math.pow(k, 1.675);
  },
  easeOut(k: number) {
    return 1 - Math.pow(1 - k, 1.675);
  },
  easeInOut(k: number) {
    return 0.5 * (Math.sin((k - 0.5) * Math.PI) + 1);
  },
};

interface IAnimateProps {
  timing?: keyof typeof timingFunctions;
  draw: (step: number) => void;
  duration: number;
}

export function animate({
  timing = "easeInOut",
  draw,
  duration,
}: IAnimateProps) {
  const start = performance.now();
  const timingFunc = timingFunctions[timing];

  return new Promise((resolve) => {
    requestAnimationFrame(function animate(time) {
      // timeFraction   0  1
      let timeFraction = (time - start) / duration;
      if (timeFraction > 1) timeFraction = 1;
      if (timeFraction < 0) timeFraction = 0;

      //    
      const progress = timingFunc(timeFraction);

      draw(progress); //  

      if (timeFraction < 1) {
        requestAnimationFrame(animate);
      } else {
        resolve(true);
      }
    });
  });
}

// :      
export function usePrevious(value: any) {
  const ref = useRef();
  useEffect(() => {
    ref.current = value;
  }, [value]);
  return ref.current;
}
